<template>
    <div class="jgbox">
      <div><img src="../assets/img/zcs.png" alt=""></div>
        <div class="from">
            <div class="title">{{ info.from_info.city_name }}</div>
            <p>{{ info.from_info.high_in_desc }}</p>
            <p>{{ info.from_info.low_in_desc }}</p>
            <p>{{ info.from_info.out_desc }}</p>
        </div>
        <div class="to">
            <div class="title">{{ info.to_info.city_name }}</div>
            <p>{{ info.to_info.high_in_desc }}</p>
            <p>{{ info.to_info.low_in_desc }}</p>
            <p>{{ info.to_info.out_desc }}</p>
        </div>
    </div>
   
</template>

<script>

import api from "../api"

export default {
    data(){
        return{
            info:{
                from_info:{},
                to_info:{}
            }
        }
    },
    mounted(){
        console.log(this.citys);
        api.query({
            from:this.citys.from.value,
            to:this.citys.to.value,
            key:"171e165a7d991c5f6ecd5194c54778ef"
        }).then(res =>{
            if(res.status === 200){
                this.info = res.data.result
            }
        })
    },
    props:{
        citys:{
            type:Object,
            default:function(){
                return {}
            }
        }
    }
};
</script>

<style scoped>
.jgbox{
  width: 100%;
  height: 100%;
}
.title {
      text-align: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    /* align-items: center; */
    -webkit-box-pack: start;
    -ms-flex-pack: start;
    justify-content: flex-start;
    /* padding: 0 0.16rem; */
    color: #000;
    font-size: 0.2rem;
    font-weight: 888;
    line-height: 0.33rem;
    /* background: #fff; */
}

.from{
    border: 0.01rem solid #ebebeb;
    border-radius: 0.08rem;
    margin: 0.03rem;

}
.from p{
  text-align:justify!important;
}
.to{
    border: 0.01rem solid #ebebeb;
